    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: 16vw;
    right: 3.822vw;
    z-index: 10;
    cursor: pointer;
}

.connection__phone svg {
    width: 50px;
    height: 50px;
}

.connection__phone:before,
.connection__phone:after {
    content: " ";
    display: block;
    position: absolute;
    border: 1px solid var(--pink);
    border: 1px solid var(--color-pink);
    left: -20px;
    right: -20px;
    top: -20px;
    bottom: -20px;
    border-radius: 50%;
    animation: animate 1.5s linear infinite
}


@keyframes animate {

    0% {
        transform: scale(0.5);
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    100% {
        transform: scale(1.2);
        opacity: 0;
    }
